:root {
	--board-background: none;
	--border-color: #392427;
	--border-thickness: 7px;
}

.board {
	width: 30vw;
	height: 50%;
	background-color: var(--board-background);
	display: flex;
	align-items: flex-start;
	flex-direction: row;
	flex-wrap: wrap;
}

.input {
	height: 33.33%;
	width: 33.33%;
	display: flex;
	justify-content: center;
	align-items: center;
	color: blue;
	font-family: 'Bellefair', serif;
	font-style: italic;
	font-weight: 700;
	font-size: 5rem;
}

.input-1 {
	border-right: var(--border-thickness) dashed var(--border-color);
	border-bottom: var(--border-thickness) dashed var(--border-color);
}

.input-2 {
	border-right: var(--border-thickness) dashed var(--border-color);
	border-bottom: var(--border-thickness) dashed var(--border-color);
}

.input-3 {
	border-bottom: var(--border-thickness) dashed var(--border-color);
}

.input-4 {
	border-right: var(--border-thickness) dashed var(--border-color);
	border-bottom: var(--border-thickness) dashed var(--border-color);
}

.input-5 {
	border-right: var(--border-thickness) dashed var(--border-color);
	border-bottom: var(--border-thickness) dashed var(--border-color);
}

.input-6 {
	border-bottom: var(--border-thickness) dashed var(--border-color);
}

.input-7 {
	border-right: var(--border-thickness) dashed var(--border-color);
}

.input-8 {
	border-right: var(--border-thickness) dashed var(--border-color);
}

